<template>
  <div class="home">
    <Header 
      :avatar="avatar"
      :name="name"
      :info="info"
      :changeAvatar="changeAvatar"
    />
    <Time 
      :date="date"
      :time="time"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import Header from "../components/home/Header";
import Time from "../components/home/Time";
import avatar from "../assets/avatar.jpg";
export default {
  name: 'Home',
  components: {
    Header,
    Time
  },
  data(){
    return{
      avatar:avatar,
      name:'leedom',
      info:"My Chinese name is 蒋利栋. I'm an enthusiastic man who likes sports and literature.",
      date:this.formatDate(new Date()),
      time:this.formatTime(new Date())
    }
  },
  methods:{
    changeAvatar(res){
      this.avatar = res
    },
    formatNumber(n){
      return n<10?('0'+n):n
    },
    formatDate(date){
      return date.getFullYear() + '-' + this.formatNumber(date.getMonth()+1) + '-' + this.formatNumber(date.getDate())
    },
    formatTime(time){
      return this.formatNumber(time.getHours()) + ':' + this.formatNumber(time.getMinutes()) + ':' + this.formatNumber(time.getSeconds())
    }
  },
  created(){
    setInterval(()=>{
      this.time = this.formatTime(new Date())
    },1000)
  }
}
</script>

<style lang="less" scoped>
  .home{
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
</style>